  <li>2. –í–æ–¥–∏—Ç–µ–ª—å –≤—ã–ø–æ–ª–Ω—è–µ—Ç —Ü–µ–ª–µ–≤–æ–µ –¥–µ–π—Å—Ç–≤–∏–µ —á–µ—Ä–µ–∑ —Ç–∞—á—Å–∫—Ä–∏–Ω</li>
